@import "components/variables";

.slider {
    display: none;
    margin-top: 3rem;
    @media (max-width: 1000px) {
        display: block;
    }
}
.dot {
    width: 10px;
    height: 10px;
    background-color: $primary;
    border-radius: 100%;
}
.dots {
    width: 100%;
    margin-top: 2rem;
}
